.movies {
  overflow: visible;
}

.movies .swiper-slide:nth-child(n + 10) .num {
  top: 12%;
  left: -4%;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  font-size: 19vw;
}

.movies .movie {
  position: relative;
}

.movies .movie .num {
  display: none;
}

.movies .movie .img img {
  width: 100%;
}

.movies .movie .img .text_wrap {
  display: none;
}

.movies .movie .img .genre {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.movies .movie .img .desc {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.rank .movie .num {
  font-family: "Roboto", sans-serif;
  font-size: 23vw;
  font-weight: 900;
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  color: #000;
  text-shadow: 0 0 10px #e1e1e1;
  letter-spacing: -2vw;
  cursor: default;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.rank .movie .img {
  width: 60%;
  margin: 0 0 0 auto;
  position: relative;
  z-index: 2;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  -webkit-transition-timing-function: ease-out;
          transition-timing-function: ease-out;
  -webkit-transition-delay: 0.5s;
          transition-delay: 0.5s;
}

.rank .movie .img:hover {
  -webkit-transform: scale(150%);
          transform: scale(150%);
}
